 div.slideIn{
     -webkit-transform: translate3d(0.6rem,0,0);
             transform: translate3d(0.6rem,0,0);
     opacity: 0;
     -webkit-animation: fromRight 1s 0s;
             animation: fromRight 1s 0s;
     -webkit-animation-fill-mode: forwards;
             animation-fill-mode: forwards;
}
 @-webkit-keyframes fromRight{
     form {
         -webkit-transform: translate3d(0.6rem,0,0);
                 transform: translate3d(0.6rem,0,0);
         opacity: 0;
    }
     to {
         -webkit-transform: translate3d(0,0,0);
                 transform: translate3d(0,0,0);
         opacity: 1;
    }
}
 @keyframes fromRight{
     form {
         -webkit-transform: translate3d(0.6rem,0,0);
                 transform: translate3d(0.6rem,0,0);
         opacity: 0;
    }
     to {
         -webkit-transform: translate3d(0,0,0);
                 transform: translate3d(0,0,0);
         opacity: 1;
    }
}
 .itemFromLeft{
     -webkit-transform: translate3d(0.6rem,0,0);
             transform: translate3d(0.6rem,0,0);
     opacity: 0;
     -webkit-animation: itemFromLeft .6s 0s linear;
             animation: itemFromLeft .6s 0s linear;
     -webkit-animation-fill-mode: forwards;
             animation-fill-mode: forwards;
}
 @-webkit-keyframes itemFromLeft {
     form {
         -webkit-transform: translate3d(-0.6rem,0,0);
                 transform: translate3d(-0.6rem,0,0);
         opacity: 0;
    }
     to {
         -webkit-transform: translate3d(0,0,0);
                 transform: translate3d(0,0,0);
         opacity: 1;
    }
}
 @keyframes itemFromLeft {
     form {
         -webkit-transform: translate3d(-0.6rem,0,0);
                 transform: translate3d(-0.6rem,0,0);
         opacity: 0;
    }
     to {
         -webkit-transform: translate3d(0,0,0);
                 transform: translate3d(0,0,0);
         opacity: 1;
    }
}
 div.contentSlideFromLeft{
     -webkit-animation: contentSlideFromLeft .4s;
             animation: contentSlideFromLeft .4s;
     -webkit-animation-fill-mode: forwards;
             animation-fill-mode: forwards;
}
 @-webkit-keyframes contentSlideFromLeft{
     from {
         -webkit-transform: translate3d(-50%,0,0);
                 transform: translate3d(-50%,0,0);
         opacity: 0;
    }
     to{
         -webkit-transform: translate3d(0,0,0);
                 transform: translate3d(0,0,0);
         opacity: 1;
    }
}
 @keyframes contentSlideFromLeft{
     from {
         -webkit-transform: translate3d(-50%,0,0);
                 transform: translate3d(-50%,0,0);
         opacity: 0;
    }
     to{
         -webkit-transform: translate3d(0,0,0);
                 transform: translate3d(0,0,0);
         opacity: 1;
    }
}
 div.contentSlideFromRight{
     -webkit-animation: contentSlideFromRight .4s;
             animation: contentSlideFromRight .4s;
     -webkit-animation-fill-mode: forwards;
             animation-fill-mode: forwards;
}
 @-webkit-keyframes contentSlideFromRight{
     from {
         -webkit-transform: translate3d(50%,0,0);
                 transform: translate3d(50%,0,0);
         opacity: 0;
    }
     to{
         -webkit-transform: translate3d(0,0,0);
                 transform: translate3d(0,0,0);
